import LineChart from "../components/charts/LineChart";
import MySelect from "../components/MySelect";
export default {
  name: 'index',
  components: {MySelect,LineChart},
  data(){
    return {
      userTypeList: [],
      classList: [],
      userType: 1,
      format(percentage) {
        return percentage === 100 ? '' : `${percentage}%`;
      }
    }
  },
  created() {
    this.layoutInit();
  },
  methods: {
    layoutInit(){
      this.$parent.$parent.layout = 'full';
    },
  }
}
